___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --orange: #f8cf85;
  --dark-orange: #f8bc4d;
  --black: #404040;
  --light-blue: #00afdf;
  --blue: #2794b3;
  --red: #ff3f3f;
  --green: #88ff88;
  --white: #e6e6e6;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    'Open Sans',
    'Helvetica Neue',
    sans-serif;
}

body {
  background-color: var(--orange);
  color: var(--black);
}

h1 {
  text-align: center;
  font-size: 5rem;
}

.score {
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  gap: 80px;
}
.score .left,
.score .right {
  width: 200px;
  padding: 5px 10px;
  margin: 30px 0px;
  font-weight: lighter;
  border: 0.1px solid var(--black);
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
}

.score .value {
  color: var(--light-blue);
  font-weight: 900;
  border-left: 0.1px solid var(--black);
  padding-left: 10px;
}

.gameboard {
  display: flex;
  justify-content: center;
  gap: 50px;
}

.gameboard .left,
.gameboard .right {
  display: flex;
  flex-direction: column;
  gap: 1px;
}
.row {
  display: flex;
  gap: 1px;
}
.cell {
  padding: 30px;
  border: 0.1px solid var(--black);
  border-radius: 5px;
}

.gameboard .right .cell:hover,
.gameboard .left .cell:hover {
  background-color: var(--dark-orange);
}

.miss {
  background-color: var(--red);
}
.hit {
  background-color: var(--green);
}
.boat {
  background-color: var(--white);
}
.gameboard .right .boat {
  background-color: var(--orange);
}

.gameboard .right.blur .cell {
  background-color: var(--dark-orange);
  opacity: 0.2;
}

.winner {
  align-items: center;
  font-size: 10rem;
}

.navbar {
  display: flex;
  justify-content: center;
  gap: 40px;
  padding: 30px;
}

.preview {
  background-color: var(--white);
  height: 70px;
  width: 10%;
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.preview .display {
  width: 90%;
  height: 60px;
  border: 0.1px solid black;
  border-top: none;
  border-radius: 0px 0px 5px 5px;
  background-color: var(--black);

  display: flex;
  justify-content: center;
  align-items: center;
}

.dashboard {
  height: 70px;
  width: 20%;
  background-color: var(--white);
  border-radius: 5px;
  padding: 10px;

  display: flex;
  justify-content: space-around;
}

.dashboard > * {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.dashboard .option {
  background-color: var(--blue);
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-size: 30px;
  padding: 20px;
}

.dashboard .option:hover {
  background-color: var(--light-blue);
  cursor: pointer;
}

.navbar .tag {
  font-size: 10px;
  font-weight: lighter;
}

@media (max-width: 1400px) {
  h1 {
    text-align: left;
    font-size: 3rem;
    padding: 5px 20px;
  }
  .score {
    font-size: 0.8rem;
    gap: 10px;
    right: 20px;
    top: 0px;
    position: fixed;
  }
  .score .left,
  .score .right {
    width: 120px;
    justify-content: space-evenly;
  }

  .gameboard {
    align-items: center;
    flex-direction: column;
  }

  .navbar {
    width: 200px;
    position: absolute;
    left: -40px;
    top: 50px;
    flex-direction: column;
    align-items: center;
  }

  .preview {
    height: 100px;
    width: 60px;
    padding: 10px;
    font-weight: 600;
    position: relative;
  }

  .preview .display {
    border-radius: 5px;
    width: 50px;
    height: 60px;
  }

  .preview .display img {
    width: 40px;
    height: 40px;
  }

  .preview .tag {
    margin-top: 5px;
    width: fit-content;
  }

  .dashboard {
    height: auto;
    width: auto;
    border-radius: 5px;
    padding: 10px;

    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
  }

  .navbar .tag {
    font-weight: 600;
  }

  .winner {
    font-size: 5rem;
  }
}

@media (max-width: 830px) {
  .cell {
    padding: 20px;
  }
}

@media (max-width: 650px) {
  .score {
    gap: 10px;
    position: static;
    top: 0px;
    padding: 0px;
    margin: 0;
  }
  .score .left,
  .score .right {
    width: 120px;
    justify-content: space-evenly;
  }

  .preview {
    height: 70px;
    width: 70px;
    border-radius: 5px;

    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    top: -15px;
    right: -24px;
  }

  .preview .display {
    width: 90%;
    height: 60px;
    border-radius: 5px;
  }

  .dashboard {
    height: auto;
    width: auto;
    border-radius: 5px;
    padding: 10px;

    flex-direction: row;
    position: relative;
    right: -300px;
    top: -180px;
  }

  .dashboard > * {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .dashboard .option {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    font-size: 30px;
    padding: 20px;
  }
}

@media (max-width: 500px) {
  h1 {
    text-align: left;
    font-size: 2rem;
    padding: 5px 20px;
  }

  .score {
    right: -90px;
    position: relative;
  }
  .preview {
    top: -30px;
    left: 0px;
  }
  .cell {
    padding: 15px;
  }

  .dashboard {
    height: 60px;
    padding: 5px;
  }

  .dashboard > * {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .dashboard .option {
    font-size: 20px;
    padding: 10px;
  }
}

